.o-banner {
    background: url('/static/img/wood.jpg') no-repeat fixed center center;
    background-size: cover;
    text-align: center;
    font-style: italic;
}

.o-banner__wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    padding: 1em;
}

.o-banner__img {
    width: 4em;
    height: auto;
    -ms-flex-preferred-size: 8em;
        flex-basis: 8em;
    padding: 0 1em;
}

.o-banner__heading {
    color: var(--color-white);
    padding: 0 1em;
    font-family: "georgia", "palatino";
    font-size: 3em;
}



